@import url(https://fonts.googleapis.com/css?family=Passion+One);
/* * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
} */

.App {
  width: 100%;
  max-width: 960px;
  margin: auto;
}

body {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: 200;
  background-color: black;
}

/* header {
    padding: 50px 15px 0px;

} */

header h1 {
  color: floralwhite;
  font-size: 48px;
  font-weight: 500;
  text-align: center;
}

main {
  color: #888;
  width: 100%;
  margin: 0 auto;
}

section {
  padding: 15px;
}

.searchbox {
  display: block;
  width: 100%;
  padding: 15px;

  border: none;
  outline: none;
  background-color: none;

  background-color: rgb(90, 2, 2);
  border-radius: 8px;

  color: whitesmoke;
  font-size: 20px;
  font-weight: 300;

  transition: 0.4s ease-out;
}

.searchbox:focus {
  box-shadow: 0px 0px 8px 3px slategray;
}

.results {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 0px -15px;
}

.results .result {
  width: 30%;
  min-width: 250px;
  padding: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  cursor: pointer;
}

.results .result img {
  display: block;
  width: 100%;
}

.results .result h3 {
  color: whitesmoke;
  font-size: 20px;
  font-weight: 600;
  padding: 10px;
  background: black;
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%;
  transition: 0.4s ease-out;
}

.results.result:hover h3 {
  background: whitesmoke;
  color: black;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgb(59, 58, 58);

  overflow-y: scroll;
}

.popup .content {
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 60px;

  color: white;
}

.popup .content h2 {
  font-size: 28px;
  font-weight: 600;
}

.popup .content h2 span {
  font-weight: 300;
  font-size: 20px;
}

.popup .content .rating {
  margin-bottom: 30px;
}

.popup .content .plot {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 0px -15px 30px;
}

.popup .content .plot img {
  -webkit-flex: 1 1 50%;
          flex: 1 1 50%;
  max-width: 300px;
  padding: 0px 15px;
}

.popup .content .plot p {
  -webkit-flex: 1 1 50%;
          flex: 1 1 50%;
  padding: 15px;
}

.popup .content .close {
  display: inline-block;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: 700;

  background: lightblue;
  color: white;
  border-radius: 8px;
  border: none;
  outline: none;
  -webkit-appearance: none;
          appearance: none;
  cursor: pointer;

  transition: 0.4s ease-out;
}

.popup .content .close:hover {
  background: black;
}

/* SignUp form css below this point*/

.form .form-group {
  margin: 1.2rem 0;
}

.form .form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

.form input[type="text"],
.form input[type="email"],
.form input[type="password"],
.form input[type="date"],
.form select,
.form textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 1.2rem;
  border: 1px solid #ccc;
}

.sign-in {
  font-size: 1.5rem;
  color: #888;
}

